import React from "react"

const Wave = () => {}

const WaveContainer = ({
  startColor = "n-400",
  endColor = "n-200",
  children,
}) => {
  const childrenBefore = []
  const childrenAfter = []
  let waveFound = false
  let adjust = 0

  React.Children.forEach(children, el => {
    if (el?.type === Wave) {
      waveFound = true
      adjust = el.props?.adjust || 0
    } else if (waveFound) childrenAfter.push(el)
    else childrenBefore.push(el)
  })

  if (!waveFound) throw new Error("WaveContainer needs to have a Wave child.")

  return (
    <>
      <div className={`bg-${startColor}`}>
        <div className="relative min-h-360 z-10">{childrenBefore}</div>
        <div
          className={`relative h-620 -mt-360 -mb-260 overflow-hidden bg-${endColor}`}
          style={{ top: adjust }}
        >
          <div className="container h-full">
            <div className="transform origin-top-left translate-x-24 translate-y-620">
              <div className="transform origin-left scale-x-200">
                <div
                  className={`h-1000 w-1000 bg-${startColor} rounded-2xl transform origin-top-left -rotate-135`}
                ></div>
              </div>
            </div>
          </div>
        </div>
        <div className={`min-h-260 bg-${endColor}`}>
          <div className="relative">{childrenAfter}</div>
        </div>
      </div>
    </>
  )
}

WaveContainer.Wave = Wave

export default WaveContainer
