import React, { useState, useMemo, useCallback, Fragment } from "react"
import { useInView } from "react-intersection-observer"
import { StaticImage } from "gatsby-plugin-image"

import twConfig from "../utils/twConfig.js"
import useWindowWidth from "../hooks/useWindowWidth.js"
import Button from "./common/Button"
import { IconArrowTopRight } from "../components/common/icons"

const lgScreen = parseInt(twConfig.theme.screens.lg, 10)

const TwelveTitansLogo = (
  <StaticImage
    src="../images/12-titans-logo.png"
    alt="Partner logo"
    className="w-52 h-52"
    objectFit="contain"
    placeholder="blurred"
  />
)

const partners = [
  {
    id: 1,
    title: "Ninja Tracks",
    link: "/",
    logo: TwelveTitansLogo,
  },
  {
    id: 2,
    title: "Titans Music",
    link: "/",
    logo: TwelveTitansLogo,
  },
  {
    id: 3,
    title: "Twelve Titans Music",
    link: "/",
    logo: TwelveTitansLogo,
  },
  {
    id: 4,
    title: "Cyranek",
    link: "/",
    logo: TwelveTitansLogo,
  },
  {
    id: 5,
    title: "Epic Music World",
    link: "/",
    logo: TwelveTitansLogo,
  },
  {
    id: 6,
    title: "MaSTAR",
    link: "/",
    logo: TwelveTitansLogo,
  },
]

export default () => {
  const [hoveredPartner, setHoveredPartner] = useState(0)

  const windowWidth = useWindowWidth()
  const isLgScreen = windowWidth >= lgScreen

  const partnersLines = useMemo(() => {
    const buildLine = partners =>
      [...Array(10).keys()].reduce(acc => [...acc, ...partners], [])
    return isLgScreen
      ? [
          buildLine(partners.slice(0, Math.trunc(partners.length / 2))),
          buildLine(
            partners.slice(Math.trunc(partners.length / 2), partners.length)
          ),
        ]
      : [buildLine(partners)]
  }, [isLgScreen])

  const partnerIsHovered = useCallback(
    partner => partner.id === hoveredPartner,
    [hoveredPartner]
  )

  const lineDuration = useMemo(
    () => partnersLines.map(l => l.toString().length / 4),
    [partnersLines]
  )

  const [animationRef, animationInView] = useInView()

  return (
    <section className="flex flex-col relative items-center space-y-10 -mt-20">
      <p className="py-3 px-12 bg-n-100 text-n-600 text-xs font-mono border border-n-300 rounded-full">
        Our Partners
      </p>
      {(partners.find(partnerIsHovered) || partners[0]).logo}
      <div ref={animationRef} className="w-full overflow-hidden space-y-6">
        {partnersLines.map((partnersLine, lineIndex) => {
          const outerAnimationState = partnersLine.find(partnerIsHovered)
            ? "animation-running"
            : "animation-paused"

          const outerAnimationDirection =
            lineIndex % 2 === 0 ? "animation-reverse" : "animation-normal"

          const innerAnimationState = animationInView
            ? "animation-running"
            : "animation-paused"

          const innerAnimationDirection =
            lineIndex % 2 === 0 ? "animation-normal" : "animation-reverse"

          return (
            <div
              key={lineIndex}
              className={`inline-flex animate-move-line ${outerAnimationState} ${outerAnimationDirection}`}
              style={{
                animationDuration: `${lineDuration[lineIndex] * 1.5}s`,
              }}
            >
              <div
                className={`inline-flex lg:min-h-36 lg:text-8xl text-6xl whitespace-nowrap font-light text-n-300 animate-move-line ${innerAnimationState} ${innerAnimationDirection}`}
                style={{
                  animationDuration: `${lineDuration[lineIndex]}s`,
                }}
              >
                {partnersLine.map((partner, partnerIndex) => (
                  <Fragment key={`${partner.id}${partnerIndex}`}>
                    <a
                      tabIndex={-1}
                      href={partner.link}
                      className="hover:text-n-900 flex flex-col items-center px-2"
                      onMouseOver={() => setHoveredPartner(partner.id)}
                      onMouseLeave={() => setHoveredPartner(0)}
                      onTouchStart={() => setHoveredPartner(partner.id)}
                      onTouchEnd={() => setHoveredPartner(0)}
                      onFocus={() => setHoveredPartner(partner.id)}
                      onBlur={() => setHoveredPartner(0)}
                    >
                      {partner.title}
                      <Button
                        tabIndex={-1}
                        type="grey"
                        className={`hidden lg:block mt-2
                        ${
                          hoveredPartner === partner.id
                            ? "opacity-1"
                            : "opacity-0"
                        }`}
                      >
                        Website
                        <IconArrowTopRight className="relative bottom-px text-90 ml-2" />
                      </Button>
                    </a>
                    /
                  </Fragment>
                ))}
              </div>
            </div>
          )
        })}
      </div>
    </section>
  )
}
