import React from "react"

const createSVG = (viewBox, rotate, pathsFragment) => props => {
  const [minX, minY, width, height] = viewBox
    .split(" ")
    .filter(i => !!i)
    .map(i => parseInt(i, 10))
  const rotateX = minX + width / 2
  const rotateY = minY + height / 2
  return (
    <svg
      {...props}
      style={{ display: "inline", height: "1em" }}
      xmlns="http://www.w3.org/2000/svg"
      viewBox={viewBox}
    >
      <g
        fill="currentColor"
        transform={`rotate(${rotate}, ${rotateX}, ${rotateY})`}
      >
        {pathsFragment}
      </g>
    </svg>
  )
}

