import React, { forwardRef } from "react"
import { StaticImage } from "gatsby-plugin-image"

import twConfig from "../utils/twConfig.js"
import useWindowWidth from "../hooks/useWindowWidth.js"
import WaveContainer from "../components/common/WaveContainer"
import Button from "./common/Button"

import { IconArrowShort, IconLogo, IconPlay } from "../components/common/icons"
import videoImage from "../images/video-image.png"

const lgScreen = parseInt(twConfig.theme.screens.lg, 10)

const Box = ({ className, children, ...props }) => (
  <div
    className={`flex-1 flex-grow pt-36 pb-16 px-14 rounded-xl ${
      className || ""
    }`}
    {...props}
  >
    {children}
  </div>
)

const Item = ({ number, children }) => (
  <p className="text-n-100 text-2xl xs:text-xl font-light sm:w-1/2 lg:w-1/3 sm:px-10 mb-16 lg:mb-24">
    <span className="font-mono text-primary text-sm font-normal">
      {String(number).padStart(2, "0")}{" "}
    </span>
    {children}
  </p>
)

export default forwardRef((props, ref) => {
  const windowWidth = useWindowWidth()

  return (
    <div ref={ref} className="overflow-x-hidden">
      <WaveContainer startColor="n-200" endColor="n-800">
        <div className="container py-28 text-3.5xl">
          <div className="md:px-14">
            <div className="relative left-px text-5xl mb-6">
              <IconLogo className="relative left-px" />
            </div>
            Epic Elite is a one-stop shop for all things{" "}
            <br className="hidden lg:inline" />
            YouTube.{" "}
            <span className="text-n-400 font-light">
              We offer personalized and dedicated services to each of our
              clients.
            </span>
          </div>
        </div>

        {windowWidth >= lgScreen && <WaveContainer.Wave adjust={50} />}

        <div className="container flex flex-col lg:flex-row lg:space-x-6 space-y-20 lg:space-y-0 relative z-10">
          <Box className="bg-n-900 text-n-200">
            <h2 className="text-2.5xl font-medium mb-5">
              Epic Elite only serves a very limited and exclusive clientele.
            </h2>
            <p className="text-xl font-light">
              If you feel you meet the criteria, kindly apply for an account and
              one of our representatives will get in touch.
            </p>
            <Button type="secondary" className="mt-11 lg:mt-14">
              Apply for Account
              <IconArrowShort className="ml-3 relative bottom-px text-90" />
            </Button>
          </Box>
          <Box className="bg-primary">
            <p className="text-2xl">
              Our holistic approach towards managing your catalog online not
              only results in generating revenue but also boosting your numbers
              across all social media platforms.
            </p>
            <Button className="mt-11 lg:mt-14">
              How We Operate
              <IconArrowShort className="ml-3 relative bottom-px text-90" />
            </Button>
          </Box>
        </div>

        {windowWidth < lgScreen && <WaveContainer.Wave adjust={-150} />}

        <div className="container pt-60 pb-12 lg:pb-40 relative">
          <p className="text-4xl text-n-100 lg:w-1/2 font-medium">
            A <span className="text-primary">holistic approach </span>
            towards managing your online catalog.
          </p>
          <StaticImage
            src="../images/about-image.png"
            alt="Dashboard"
            style={{
              position: windowWidth < lgScreen ? "relative" : "absolute",
            }}
            className="w-600 top-1/2 left-1/2 transform -translate-x-1/3 lg:-translate-x-0 lg:-translate-y-1/2"
            placeholder="blurred"
          />
        </div>
      </WaveContainer>

      <WaveContainer startColor="n-800" endColor="n-200">
        <div className="container">
          <div className="flex flex-row justify-between flex-wrap pt-6 sm:-mx-10 px-8 sm:px-0">
            <Item number={1}>
              We combine quality image capture with a minimalistic production,
              capturing the most authentic moments.
            </Item>
            <Item number={2}>
              Together, we are intensely curious and on a search towards unique
              designs, concepts and materials.
            </Item>
            <Item number={3}>
              We are intrinsically drawn to beauty and always on the lookout for
              original ideas.
            </Item>
          </div>
        </div>

        <div className="container">
          <div className="rounded-xl shadow-2xl">
            <div className="rounded-xl overflow-hidden relative h-430">
              <img
                src={videoImage}
                alt="Video"
                className="object-cover min-h-full"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-n-900 to-transparent flex items-end">
                <div className="w-full py-4 px-6 text-n-200 flex items-center justify-between">
                  <div className="flex items-center">
                    <button
                      tabIndex={-1}
                      className="focus:outline-none absolute lg:static top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 lg:translate-x-0 lg:translate-y-0"
                    >
                      <IconPlay className="text-n-200 text-4xl" />
                    </button>
                    <p className="text-lg font-light lg:ml-4">
                      See how we help musicians, labels and channels.
                    </p>
                  </div>
                  <span className="font-mono text-sm hidden lg:inline">
                    00:17 / 01:24
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <WaveContainer.Wave adjust={-200} />
      </WaveContainer>
    </div>
  )
})
